#main-nav {
  background-color:cornflowerblue;
}

#main-nav ul {
  margin:0;
  padding-left:0;
}

#main-nav li {
  list-style-type:none;
  padding:5px;
  flex:1;
  padding:5px;
  border-bottom:1px solid white;
  text-align:center;
}

#main-nav li a {
  text-decoration: none;
  color:white;
}

#main-nav li a:hover {
  color:#f4d570;
}

#main-nav .hide-small {
  display:none;
}

@media screen and (min-width: 768px) {
  #main-nav button {
      display:none;
  }

  #main-nav .hide-small {
      display:flex;
  }

  #main-nav li {
      border-bottom:0;
  }
}